@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);
::-webkit-scrollbar {
  width: 0px;
}

.Text {
  font-family: "Noto Sans KR", sans-serif !important;
}

.fade-enter,
.fade-appear {
  opacity: 0;
}
.fade-enter-active,
.fade-appear-active {
  opacity: 1;
  transition: opacity 2000ms;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  transition: opacity 2000ms;
}

.page {
  width: 100%;
  height: 100%;
  z-index: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.pictureContainer {
  margin-top: 2%;
  display: flex;
  overflow-x: scroll;
  width: 100%;
  padding: 30px 0px;
}

.picture {
  margin-left: 10px;
  margin-right: 10px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}


.App {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: cover;
  overflow-y: hidden; 
  overflow-x: hidden;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
}

.PhoneImg_col {
  margin-left: 7%;
  width: auto;
  height: auto;
}
.PhoneImg_col .PhoneImg {
  position: block;
  max-width: 90%;
  width: auto;
  height: auto;

}
.Explanation_col {
  margin-top: 10%;
  margin-right: 15%;
  width: auto;
  height: auto;

}
.Explanation_col .Title_row{
  position: block;
  margin-bottom: 15%;
}
.Explanation_col .Title_row .Title{
  color: white;
  font-weight: bold;
  font-size: 5vw;
  text-shadow: 6px 2px 2px gray;

}

.Explanation_col .Content_row{
  position: block;
  margin-bottom: 15%;
  margin-top: 15%;
  position: block;
}
.Explanation_col .Content_row .Content{
  color: white;
  font-size: 3vw;
  text-shadow: 4px 1px 1px gray;
}

.Explanation_col .Qr_row .QrPicture{
  max-width: 40%;
  width: auto;
  height: auto;
  box-shadow: 0px 0px 30px grey;
}
.Explanation_col .Qr_row .QrExplanation{
  width: auto;
  height: auto;
  color: white;
  margin-top: 5%;
  margin-left: -60%;
  width: 220%;
  font-size: 2vw;
  text-shadow: 2px 0.5px 0.5px gray;
}
/*=======page 3-1 flower ========*/

.cloud {
  position: relative;/*포지션이 absolute, fixed, relative인 경우만 해당*/
  z-index: 0;/*숫자가 높을 수록 앞으로 나온다*/
  width: auto; height: auto;
  max-width:90%;
  max-height:90%;
  margin-top: 3rem;

}
.butterfly{
  width: 10%;
  height:10%;
}


/*=======page 3-2 ========*/


/*======page 3-3 ocean========*/
.background {
    width: 100%;
    position: absolute;
    height: -webkit-fill-available;
}

.boat {
	width: 20%;
  left: 70%;
  margin-top: 46%;
	position: relative;
}
.boat-entered {
  left: 70%;
  margin-top: 46%;

}
.boat-entering {
  -webkit-animation-name: boat_moving;
          animation-name: boat_moving;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
}
@-webkit-keyframes boat_moving {
  0%, 40%, 80% {
    left: 10%;
    margin-top: 46%;
  }
  20%, 60%, 100% {
    left: 70%;
    margin-top: 46%;
  }
}
@keyframes boat_moving {
  0%, 40%, 80% {
    left: 10%;
    margin-top: 46%;
  }
  20%, 60%, 100% {
    left: 70%;
    margin-top: 46%;
  }
}


/*======page 3-4 forest========*/
.basechild {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
}

.secondchild {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 3;
}

.lightchild {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 2;
}

.albumchild {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 5;
}

.baseimg,
.lightimg,
.secondimg,
.albumimg {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  z-index: -1;
}




